@use '../abstracts' as *;

.trumbowyg-box, .editor {
  display: block;
  position: relative;
  width: 100%;
  min-height: 300px;

  button {
    margin-top: 0 !important;
  }
}

.trumbowyg-box .editor {
  margin: 0 auto;
  background: #FEFEFE
}

.trumbowyg-box.trumbowyg-fullscreen {
  background: #FEFEFE
}

.trumbowyg-editor {
  position: relative;
  padding: 1.5rem;
  min-height: 60vh;
  width: 100%;
  border: 1px solid var(--border) !important;
  border-style: none;
  resize: none;
  outline: none;
  border-top: none !important;
}

.trumbowyg-box-blur .trumbowyg-editor * {
  color: transparent !important;
  text-shadow: 0 0 7px #333
}

.trumbowyg-box-blur .trumbowyg-editor img {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=20);
  opacity: 0.2
}

.trumbowyg-textarea {
  height: 100% !important;
  position: relative;
  display: block;
  overflow: auto;
  border: none;
  white-space: normal
}

.trumbowyg-button-pane {
  position: relative;
  width: 100%;
  background: var(--lighter-color);
  margin: 0;
  padding: 0;
  list-style-type: none;
  line-height: 10px;
  display: flex;
  @extend %border;

  & * {
    height: 35px;
  }

  button {
    padding: 1rem;
  }

  li {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
    width: auto;
    padding: 0;

    &:hover {
      background: var(--base-color);
    }

    &.trumbowyg-separator {
      width: 1px;
      padding: 0;
      background: var(--border);
      height: 35px
    }
  }
}

.trumbowyg-button-pane.trumbowyg-disable li:not(.trumbowyg-not-disable) button {
  opacity: .2;
  cursor: default
}

.trumbowyg-button-pane li .trumbowyg-open-dropdown {
  padding: 1rem 2rem 1rem 1rem;
  &:after {
    display: block;
    content: " ";
    position: absolute;
    top: 4px;
    bottom: 0;
    margin: auto;
    right: .5rem;
    height: 0;
    width: 0;
    border: 5px solid transparent;
    border-top-color: var(--text);
  }
}

.trumbowyg-button-pane .trumbowyg-buttons-right {
  float: right;
  width: auto
}

.trumbowyg-button-pane .trumbowyg-buttons-right button {
  float: left
}

.trumbowyg-dropdown {
  width: 200px;
  border: 1px solid #ecf0f1;
  padding: 5px 0;
  border-top: none;
  background: var(--solid);
  margin-left: -1px;
  -moz-box-shadow: rgba(0,0,0,0.1) 0 2px 3px;
  -webkit-box-shadow: rgba(0,0,0,0.1) 0 2px 3px;
  box-shadow: rgba(0,0,0,0.1) 0 2px 3px
}

.trumbowyg-dropdown button {
    display: block;
    width: 100%;
    height: 35px;
    line-height: 35px;
    text-decoration: none;
    background: var(--solid);
    padding: 0 14px;
    color: #333;
    border: none;
    cursor: pointer;
    text-align: left;
    font-size: 15px;
    @extend %transition;
}

.trumbowyg-dropdown button:hover, .trumbowyg-dropdown button:focus {
  background: #ecf0f1
}

.trumbowyg-modal {
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -260px;
  width: 520px;
  height: 290px;
  overflow: hidden
}

.trumbowyg-modal-box {
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -250px;
  width: 500px;
  height: 275px;
  z-index: 1;
  background-color: var(--solid);
  text-align: center;
  -moz-box-shadow: rgba(0,0,0,0.2) 0 2px 3px;
  -webkit-box-shadow: rgba(0,0,0,0.2) 0 2px 3px;
  box-shadow: rgba(0,0,0,0.2) 0 2px 3px
}

.trumbowyg-modal-box .trumbowyg-modal-title {
  font-size: 24px;
  font-weight: bold;
  margin: 0 0 20px;
  padding: 15px 0 13px;
  display: block;
  border-bottom: 1px solid #EEE;
  color: #333;
  background: #fbfcfc
}

.trumbowyg-modal-box .trumbowyg-progress {
  width: 100%;
  background: #F00;
  height: 3px;
  position: absolute;
  top: 58px
}

.trumbowyg-modal-box .trumbowyg-progress .trumbowyg-progress-bar {
    background: #2BC06A;
    height: 100%;
    @extend %transition;
}

.trumbowyg-modal-box label {
  display: block;
  position: relative;
  margin: 15px 12px;
  height: 27px;
  line-height: 27px;
  overflow: hidden
}

.trumbowyg-modal-box label .trumbowyg-input-infos {
    display: block;
    text-align: left;
    height: 25px;
    line-height: 25px;
    @extend %transition;
}

.trumbowyg-modal-box label .trumbowyg-input-infos span {
  display: block;
  color: #859fa5;
  background-color: #fbfcfc;
  border: 1px solid #DEDEDE;
  padding: 0 2%;
  width: 19.5%
}

.trumbowyg-modal-box label .trumbowyg-input-infos span.trumbowyg-msg-error {
  color: #e74c3c
}

.trumbowyg-modal-box label.trumbowyg-input-error input, .trumbowyg-modal-box label.trumbowyg-input-error textarea {
  border: 1px solid #e74c3c
}

.trumbowyg-modal-box label.trumbowyg-input-error .trumbowyg-input-infos {
  margin-top: -27px
}

.trumbowyg-modal-box label input {
    position: absolute;
    top: 0;
    right: 0;
    height: 25px;
    line-height: 25px;
    border: 1px solid #DEDEDE;
    background: transparent;
    width: 72%;
    padding: 0 2%;
    margin: 0 0 0 23%;
    @extend %transition;
}

.trumbowyg-modal-box label input:hover, .trumbowyg-modal-box label input:focus {
  outline: none;
  border: 1px solid #95a5a6
}

.trumbowyg-modal-box label input:focus {
  background: rgba(230,230,255,0.1)
}

.trumbowyg-modal-box .error {
  margin-top: 25px;
  display: block;
  color: red
}

.trumbowyg-modal-box .trumbowyg-modal-button {
    position: absolute;
    bottom: 10px;
    right: 0;
    text-decoration: none;
    color: var(--solid);
    display: block;
    width: 100px;
    height: 35px;
    line-height: 33px;
    margin: 0 10px;
    background-color: #333;
    border: none;
    border-top: none;
    cursor: pointer;
    font-size: 16px;
    @extend %transition;
}

.trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-submit {
  right: 110px;
  background: #2bc06a
}

.trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-submit:hover, .trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-submit:focus {
  background: #40d47e;
  outline: none
}

.trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-submit:active {
  background: #25a25a
}

.trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-reset {
  background: #b33528
}

.trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-reset:hover, .trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-reset:focus {
  background: #d14233;
  outline: none
}

.trumbowyg-modal-box .trumbowyg-modal-button.trumbowyg-modal-reset:active {
  background: #962d22
}

.trumbowyg-overlay {
  position: absolute;
  background-color: rgba(255,255,255,0.5);
  width: 100%;
  left: 0;
  display: none
}

.trumbowyg-fullscreen.trumbowyg-box, .trumbowyg-fullscreen .editor {
  border: none
}

.trumbowyg-fullscreen .trumbowyg-overlay {
  height: 100% !important
}

.trumbowyg-editor object, .trumbowyg-editor embed, .trumbowyg-editor video, .trumbowyg-editor img {
  width: auto;
  max-width: 100%
}

.trumbowyg-editor video, .trumbowyg-editor img {
  height: auto
}

.trumbowyg-editor img {
  cursor: move
}

.trumbowyg-editor.trumbowyg-reset-css {
  background: #FEFEFE !important;
  font-size: 14px !important;
  line-height: 1.45em !important;
  white-space: normal !important;
  color: #333
}

.trumbowyg-editor.trumbowyg-reset-css a {
  color: #15c !important;
  text-decoration: underline !important
}

.trumbowyg-editor.trumbowyg-reset-css div, .trumbowyg-editor.trumbowyg-reset-css p, .trumbowyg-editor.trumbowyg-reset-css ul, .trumbowyg-editor.trumbowyg-reset-css ol, .trumbowyg-editor.trumbowyg-reset-css blockquote {
  box-shadow: none !important;
  background: none !important;
  margin: 0 !important;
  margin-bottom: 15px !important;
  line-height: 1.4em !important;
  font-size: 14px !important;
  border: none
}

.trumbowyg-editor.trumbowyg-reset-css iframe, .trumbowyg-editor.trumbowyg-reset-css object, .trumbowyg-editor.trumbowyg-reset-css hr {
  margin-bottom: 15px !important
}

.trumbowyg-editor.trumbowyg-reset-css blockquote {
  margin-left: 32px !important;
  font-style: italic !important;
  color: #555
}

.trumbowyg-editor.trumbowyg-reset-css ul, .trumbowyg-editor.trumbowyg-reset-css ol {
  padding-left: 20px !important
}

.trumbowyg-editor.trumbowyg-reset-css ul ul, .trumbowyg-editor.trumbowyg-reset-css ol ol, .trumbowyg-editor.trumbowyg-reset-css ul ol, .trumbowyg-editor.trumbowyg-reset-css ol ul {
  border: none;
  margin: 2px !important;
  padding: 0 !important;
  padding-left: 24px !important
}

.trumbowyg-editor.trumbowyg-reset-css hr {
  display: block;
  height: 1px;
  border: none;
  border-top: 1px solid #CCC
}

.trumbowyg-editor.trumbowyg-reset-css h1, .trumbowyg-editor.trumbowyg-reset-css h2, .trumbowyg-editor.trumbowyg-reset-css h3, .trumbowyg-editor.trumbowyg-reset-css h4 {
  color: #111;
  background: none;
  margin: 0 !important;
  padding: 0 !important;
  font-weight: bold
}

.trumbowyg-editor.trumbowyg-reset-css h1 {
  font-size: 32px !important;
  line-height: 38px !important;
  margin-bottom: 20px !important
}

.trumbowyg-editor.trumbowyg-reset-css h2 {
  font-size: 26px !important;
  line-height: 34px !important;
  margin-bottom: 15px !important
}

.trumbowyg-editor.trumbowyg-reset-css h3 {
  font-size: 22px !important;
  line-height: 28px !important;
  margin-bottom: 7px !important
}

.trumbowyg-editor.trumbowyg-reset-css h4 {
  font-size: 16px !important;
  line-height: 22px !important;
  margin-bottom: 7px !important
}

.trumbowyg-button-pane li button {
    display: block;
    position: relative;
    text-indent: -9999px;
    width: 35px;
    height: 35px;
    overflow: hidden;
    background: transparent url('/Content/htmleditor/images/htmleditor_icons-dark.png') no-repeat;
    border: none;
    cursor: pointer;
    @extend %transition; 
}

